#notepad {
  resize: both;
  overflow: hidden;

  .content {
    padding: 0;

    textarea {
      border: 0;
      width: 100%;
      height: 100%;
      overflow: scroll;
      display: block;
      resize: none;
      box-sizing: border-box;

      &:focus {
        outline: none;
      }
    }
  }
}
